/*-------------------------------------*\
      Social
      social.css
\*-------------------------------------*/

.social {
  background-repeat: no-repeat;
  margin-left: 0.2rem;
  margin-right: 0.2rem;
}

.social__twitter {
  @include sprite($social-twitter);
}

.social__facebook {
  @include sprite($social-facebook);
}

.social__youtube {
  @include sprite($social-youtube);
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
       only screen and (min-resolution: 144dpi) {

    .social__twitter {
      @include sprite($social-twitter-2x);
    }

    .social__facebook {
      @include sprite($social-facebook-2x);
    }

    .social__youtube {
      @include sprite($social-youtube-2x);
    }
  }
