// Не пишите здесь другие селекторы.

/*-------------------------------------*\
      Icons
      icon.css
\*-------------------------------------*/

.icon {
  background-repeat: no-repeat;
}

.icon__accomodations {
  @include sprite($icon-accomodations);
}

.icon__food {
  @include sprite($icon-food);
}

.icon__presents {
  @include sprite($icon-presents);
}

@media only screen and (-webkit-min-device-pixel-ratio: 1.5),
       only screen and (min-resolution: 144dpi) {

    .icon__accomodations {
    @include sprite($icon-accomodations-2x);
    }

    .icon__food {
    @include sprite($icon-food-2x);
    }

    .icon__presents {
    @include sprite($icon-presents-2x);
    }

       }
